@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600|Raleway:400,600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.flex {
    display: flex;
}

.row {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin-left: -8px;
    margin-right: -8px;
}

.column {
    display: flex;
    flex-direction: column;
    flex: 1 1;
    padding-left: 8px;
    padding-right: 8px;
}

.clearfix::after {
    content: " ";
    display: block;
    clear: both;
}

.text-danger {
    color: #e74c3c;
}

.text-success {
    color: #27AE60;
}

.ml-3 {
    margin-left: 1rem;
}

.mr-3 {
    margin-right: 1rem;
}
* {
    box-sizing: border-box;
}

html, body {
    font-size: 14px;
    font-family: Montserrat, sans-serif;
}

h1, h2, h3, h4, h5 {
    margin-top: 0;
    margin-bottom: 1rem;
    font-family: Raleway, sans-serif;
}

.header {
    background-color: #ffffff;
    box-shadow: 0 1px 5px rgba(0,0,0,0.1);
}

.header__inner {

}

.header__brand {
    padding-top: 12px;
    padding-bottom: 12px;
    float: left;
    font-weight: 600;
    font-size: 24px;
    font-family: Raleway, sans-serif;
}

.container {
    max-width: 1200px;
    padding-left: 12px;
    padding-right: 12px;
    margin-left: auto;
    margin-right: auto;
}

.card {
    padding: 1.5rem;
    box-shadow: 0 1px 2px rgba(0,0,0,.5);
    margin-top: 1rem;
    margin-bottom: 1rem;
}

.btn {
    width: 100px;
    background: #27AE60;
    font-weight: bold;
    color: white;
    border: 0 none;
    border-radius: 1px;
    cursor: pointer;
    padding: 10px 5px;
    margin: 10px 5px;
}

.label {
    font-weight: 600;
}

.form-control {
    border: none;
    outline: none;
    padding: 10px 5px;
    border-bottom: 1px solid #000000;
    display: block;
    width: 100%;
    -webkit-transition: border-bottom 0.5s ease;
    transition: border-bottom 0.5s ease;
}

.form-control:focus {
    border-bottom: 1px solid #1195d3;
}

.avatar {
    height: 120px;
    width: 120px;
    margin-right: 1rem;
}
.input-container {
    width: 100%;
    margin: 1rem auto;
    -webkit-transition: width 0.5s ease, margin 0.5s ease 0.5s;
    transition: width 0.5s ease, margin 0.5s ease 0.5s;
}

.input-container--centralized {
    width: 50%;
    margin: 20% auto;
}



